<template>
  <div class="rules-page">
    <!-- È°µÈù¢Ê†áÈ¢ò -->
    <div class="page-header">
      <h1>Ê†∏‰øùËßÑÂàôÁÆ°ÁêÜ</h1>
      <div class="header-actions">
        <BaseButton type="primary" @click="handleCreate">Êñ∞Â¢ûËßÑÂàô</BaseButton>
      </div>
    </div>

    <!-- ÊêúÁ¥¢Ê†è -->
    <div class="search-bar">
      <BaseInput 
        v-model="searchQuery.name" 
        placeholder="ÊêúÁ¥¢ËßÑÂàôÂêçÁß∞"
        style="width: 300px"
      />
      <BaseSelect
        v-model="searchQuery.status"
        :options="statusOptions"
        placeholder="ËßÑÂàôÁä∂ÊÄÅ"
        style="width: 200px"
      />
      <BaseSelect
        v-model="searchQuery.diseaseCategory"
        :options="diseaseCategoryOptions"
        placeholder="ÁñæÁóÖÁ±ªÂà´"
        style="width: 200px"
      />
      <BaseButton @click="handleSearch">ÊêúÁ¥¢</BaseButton>
      <BaseButton @click="handleReset">ÈáçÁΩÆ</BaseButton>
    </div>

    <!-- Êï∞ÊçÆË°®Ê†º -->
    <div class="rules-table">
      <!-- Âä†ËΩΩÁä∂ÊÄÅ -->
      <div v-if="loading" class="loading-wrapper">
        <div class="skeleton-row" v-for="i in 5" :key="i">
          <div class="skeleton-cell" v-for="j in 6" :key="j"></div>
        </div>
      </div>

      <!-- Á©∫Áä∂ÊÄÅ -->
      <div v-else-if="!rules.length" class="empty-state">
        <span class="icon">üì≠</span>
        <p>ÊöÇÊó†Êï∞ÊçÆ</p>
      </div>

      <!-- Êï∞ÊçÆË°®Ê†º -->
      <table v-else>
        <thead>
          <tr>
            <th>ID</th>
            <th>Ê†∏‰øùËßÑÂàôÂêçÁß∞</th>
            <th>Ê†∏‰øùËßÑÂàôÁâàÊú¨</th>
            <th>ËßÑÂàôÁä∂ÊÄÅ</th>
            <th>Â§áÊ≥®</th>
            <th>ÂàõÂª∫Êó∂Èó¥</th>
            <th>ÁºñËæëÊó∂Èó¥</th>
            <th>Êìç‰Ωú</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="rule in rules" :key="rule.id">
            <td>{{ rule.id }}</td>
            <td>{{ rule.name }}</td>
            <td>{{ rule.version }}</td>
            <td>
              <span :class="['status-badge', getStatusColor(rule.status)]">
                {{ rule.status }}
              </span>
            </td>
            <td>{{ rule.remark || '-' }}</td>
            <td>{{ rule.createdAt }}</td>
            <td>{{ rule.updatedAt }}</td>
            <td>
              <div class="action-buttons">
                <BaseButton size="small" @click="handleView(rule)">Êü•Áúã</BaseButton>
                <BaseButton size="small" @click="handleViewConfig(rule)">Êü•ÁúãËßÑÂàô</BaseButton>
                <BaseButton size="small" @click="handleEdit(rule)">ÁºñËæë</BaseButton>
                <BaseButton size="small" @click="handleImport(rule)">ÂØºÂÖ•</BaseButton>
                <BaseButton v-if="rule.has_data" size="small" @click="handleExport(rule)">ÂØºÂá∫</BaseButton>
                <BaseButton size="small" type="danger" @click="handleDelete(rule)">Âà†Èô§</BaseButton>
              </div>
            </td>
          </tr>
        </tbody>
      </table>

      <!-- ÂàÜÈ°µ -->
      <div class="pagination">
        <BasePagination
          v-model:current="page"
          v-model:pageSize="pageSize"
          :total="total"
          @change="handlePageChange"
        />
      </div>
    </div>

    <!-- Êñ∞Â¢û/ÁºñËæëÂºπÁ™ó -->
    <BaseModal
      v-model="showModal"
      :title="modalType === 'add' ? 'Êñ∞Â¢ûËßÑÂàô' : 'ÁºñËæëËßÑÂàô'"
      width="500px"
      @confirm="handleModalConfirm"
    >
      <div class="form">
        <div class="form-item">
          <label>ËßÑÂàôÂêçÁß∞</label>
          <BaseInput
            v-model="formData.name"
            placeholder="ËØ∑ËæìÂÖ•ËßÑÂàôÂêçÁß∞"
            :maxlength="50"
          />
          <div class="form-error" v-if="formErrors.name">
            {{ formErrors.name }}
          </div>
        </div>
        
        <div class="form-item">
          <label>ÁâàÊú¨Âè∑</label>
          <BaseInput
            v-model="formData.version"
            placeholder="ËØ∑ËæìÂÖ•ÁâàÊú¨Âè∑"
            :maxlength="30"
            :disabled="modalType === 'edit'"
          />
          <div class="form-error" v-if="formErrors.version">
            {{ formErrors.version }}
          </div>
        </div>

        <div class="form-item">
          <label>Â§áÊ≥®</label>
          <BaseInput
            v-model="formData.remark"
            type="textarea"
            placeholder="ËØ∑ËæìÂÖ•Â§áÊ≥®‰ø°ÊÅØ"
            :rows="3"
          />
        </div>
      </div>
    </BaseModal>

    <!-- ÂØºÂÖ•ËßÑÂàôÂºπÁ™ó -->
    <BaseModal
      v-model="showImportModal"
      title="ÂØºÂÖ•ËßÑÂàô"
      width="500px"
      @confirm="handleImportConfirm"
      @cancel="handleImportCancel"
      :confirmLoading="importLoading"
    >
      <div class="form">
        <div class="form-item">
          <label>ÈÄâÊã©Êñá‰ª∂</label>
          <BaseUpload
            v-model="importFile"
            accept=".xlsx,.xls"
            :maxSize="10"
            placeholder="ËØ∑ÈÄâÊã©ExcelÊñá‰ª∂"
            @error="handleUploadError"
          />
        </div>
      </div>
    </BaseModal>
  </div>
</template>

<script setup lang="ts">
import { ref, reactive, onMounted, defineProps } from 'vue'
import { useRouter } from 'vue-router'
import type { Rule, DiseaseCategory, RuleStatus, RuleSearchParams } from '@/types/underwriting'
import type { ApiResponse, PaginationQuery, PaginationData } from '@/types/api'
import ruleApi from '@/api/underwriting'
import { showToast } from '@/utils/toast'

// Ë∑ØÁî±ÂÆû‰æã
const router = useRouter()

// Âä†ËΩΩÁä∂ÊÄÅ
const loading = ref(false)

// ÂºπÁ™óÊéßÂà∂
const showModal = ref(false)
const showImportModal = ref(false)
const modalType = ref<'add' | 'edit'>('add')
const importFile = ref<File | null>(null)
const importLoading = ref(false)

// Ë°®ÂçïÊï∞ÊçÆ
const formData = reactive({
  name: '',
  version: '',
  remark: ''
})

// Ë°®ÂçïÈîôËØØ
const formErrors = reactive({
  name: '',
  version: ''
})

// ÂΩìÂâçÁºñËæëÁöÑËßÑÂàôID
const currentId = ref<string>('')

// Áä∂ÊÄÅÈÄâÈ°π
const statusOptions = [
  { label: 'Â∑≤ÂØºÂÖ•', value: 'Â∑≤ÂØºÂÖ•' },
  { label: 'Â∑≤ÂêØÁî®', value: 'Â∑≤ÂêØÁî®' },
  { label: 'Â∑≤Á¶ÅÁî®', value: 'Â∑≤Á¶ÅÁî®' }
]

// ÁñæÁóÖÁ±ªÂà´ÈÄâÈ°π
const diseaseCategoryOptions = ref<{ label: string; value: string }[]>([])

// Êü•ËØ¢ÂèÇÊï∞
const searchQuery = reactive<RuleSearchParams>({
  name: '',
  status: undefined,
  diseaseCategory: undefined,
  page: 1,
  pageSize: 10
})

// ÂàÜÈ°µÂèÇÊï∞
const page = ref(1)
const pageSize = ref(10)
const total = ref(0)

// ËßÑÂàôÂàóË°®
const rules = ref<Rule[]>([])

// ÂÆö‰πâprops
const props = defineProps({
  confirmLoading: {
    type: Boolean,
    default: false
  },
  onNodeUnmounted: {
    type: Function,
    default: () => {}
  }
})

// ÊòæÁ§∫ÊèêÁ§∫‰ø°ÊÅØ
const toast = (options: { type: 'success' | 'error' | 'warning' | 'info', message: string }) => {
  showToast(options.type, options.message)
}

// Ëé∑ÂèñËßÑÂàôÂàóË°®
const fetchRules = async () => {
  console.log('ÂºÄÂßãÂä†ËΩΩÊ†∏‰øùËßÑÂàôÂàóË°®:', searchQuery)
  
  loading.value = true
  try {
    const res = await ruleApi.getRules(searchQuery)
    
    console.log('Ê†∏‰øùËßÑÂàôÂìçÂ∫î:', res)
    
    if (res.code === 200) {
      rules.value = res.data.list || []
      total.value = res.data.total || 0
      console.log('Ê†∏‰øùËßÑÂàôÂä†ËΩΩÊàêÂäü:', {
        list: rules.value,
        total: total.value
      })
    } else {
      console.error('Âä†ËΩΩÊ†∏‰øùËßÑÂàôÂ§±Ë¥•:', res.message)
      toast({ type: 'error', message: res.message || 'Âä†ËΩΩÂ§±Ë¥•' })
      rules.value = []
      total.value = 0
    }
  } catch (error: any) {
    console.error('Âä†ËΩΩÊ†∏‰øùËßÑÂàôÂàóË°®Â§±Ë¥•:', error)
    toast({ type: 'error', message: error.message || 'Âä†ËΩΩÂ§±Ë¥•' })
    rules.value = []
    total.value = 0
  } finally {
    loading.value = false
  }
}

// Ëé∑ÂèñÁñæÁóÖÁ±ªÂà´ÈÄâÈ°π
const fetchDiseaseCategories = async () => {
  try {
    const response = await ruleApi.getDiseaseCategories('all')
    if (response.code === 200 && response.data) {
      diseaseCategoryOptions.value = response.data.map((category: DiseaseCategory) => ({
        label: category.name,
        value: category.code
      }))
    } else {
      toast({ type: 'error', message: response.message || 'Ëé∑ÂèñÁñæÁóÖÁ±ªÂà´Â§±Ë¥•' })
    }
  } catch (error) {
    toast({ type: 'error', message: 'Ëé∑ÂèñÁñæÁóÖÁ±ªÂà´Â§±Ë¥•' })
  }
}

// ÊêúÁ¥¢
const handleSearch = () => {
  searchQuery.page = 1
  fetchRules()
}

// ÈáçÁΩÆ
const handleReset = () => {
  searchQuery.name = ''
  searchQuery.status = undefined
  searchQuery.diseaseCategory = undefined
  searchQuery.page = 1
  fetchRules()
}

// ÂàÜÈ°µÂèòÂåñ
const handlePageChange = () => {
  searchQuery.page = page.value
  searchQuery.pageSize = pageSize.value
  fetchRules()
}

// Êñ∞Â¢ûËßÑÂàô
const handleCreate = () => {
  modalType.value = 'add'
  formData.name = ''
  formData.version = ''
  formData.remark = ''
  showModal.value = true
}

// ÁºñËæëËßÑÂàô
const handleEdit = (rule: Rule) => {
  modalType.value = 'edit'
  currentId.value = rule.id
  formData.name = rule.name
  formData.version = rule.version
  formData.remark = rule.remark || ''
  showModal.value = true
}

// Êü•ÁúãËßÑÂàô
const handleView = (rule: Rule) => {
  console.log('[RulesView] ÁÇπÂáªÊü•ÁúãÊåâÈíÆ:', {
    id: rule.id,
    name: rule.name,
    version: rule.version
  });
  router.push(`/underwriting/rules/${rule.id}/detail`);
}

// Êü•ÁúãËßÑÂàôÈÖçÁΩÆ
const handleViewConfig = (rule: Rule) => {
  console.log('[RulesView] ÁÇπÂáªÊü•ÁúãËßÑÂàôÊåâÈíÆ:', {
    id: rule.id,
    name: rule.name,
    version: rule.version
  });
  router.push(`/underwriting/rules/${rule.id}/detail`);
}

// ÂØºÂá∫ËßÑÂàô
const handleExport = async (rule: Rule) => {
  try {
    const response = await ruleApi.exportRuleData(rule.id)
    if (response.code === 200 && response.data) {
      const blob = new Blob([response.data], { type: 'application/vnd.ms-excel' })
      const url = window.URL.createObjectURL(blob)
      const link = document.createElement('a')
      link.href = url
      link.download = `${rule.name}-${rule.version}.xlsx`
      link.click()
      window.URL.revokeObjectURL(url)
      toast({ type: 'success', message: 'ÂØºÂá∫ÊàêÂäü' })
    } else {
      toast({ type: 'error', message: response.message || 'ÂØºÂá∫Â§±Ë¥•' })
    }
  } catch (error) {
    toast({ type: 'error', message: 'ÂØºÂá∫Â§±Ë¥•' })
  }
}

// ÂØºÂÖ•ËßÑÂàô
const handleImport = (rule: Rule) => {
  console.log('[RulesView] ÁÇπÂáªÂØºÂÖ•ÊåâÈíÆ:', {
    id: rule.id,
    name: rule.name,
    version: rule.version
  });
  currentId.value = rule.id;
  importFile.value = null; // ÈáçÁΩÆÊñá‰ª∂
  showImportModal.value = true;
}

// Á°ÆËÆ§ÂØºÂÖ•
const handleImportConfirm = async () => {
  console.log('[RulesView] Á°ÆËÆ§ÂØºÂÖ•:', {
    ruleId: currentId.value,
    hasFile: !!importFile.value,
    fileName: importFile.value?.name
  });

  if (!importFile.value) {
    toast({ type: 'error', message: 'ËØ∑ÈÄâÊã©Ë¶ÅÂØºÂÖ•ÁöÑÊñá‰ª∂' })
    return
  }

  if (!currentId.value) {
    toast({ type: 'error', message: 'Êú™ÊâæÂà∞ËßÑÂàôID' })
    return
  }

  try {
    importLoading.value = true
    console.log('[RulesView] ÂºÄÂßãÂØºÂÖ•ËßÑÂàôÊï∞ÊçÆ:', {
      ruleId: currentId.value,
      fileName: importFile.value.name,
      fileSize: importFile.value.size,
      fileType: importFile.value.type
    })
    
    const response = await ruleApi.importRuleData(currentId.value, importFile.value)
    console.log('[RulesView] ÂØºÂÖ•ÂìçÂ∫î:', {
      code: response.code,
      message: response.message
    })

    if (response.code === 200) {
      toast({ type: 'success', message: 'ÂØºÂÖ•ÊàêÂäü' })
      showImportModal.value = false
      importFile.value = null
      currentId.value = ''
      await fetchRules()
    } else {
      toast({ type: 'error', message: response.message || 'ÂØºÂÖ•Â§±Ë¥•' })
    }
  } catch (error: any) {
    console.error('[RulesView] ÂØºÂÖ•Â§±Ë¥•:', error)
    toast({ type: 'error', message: error?.message || 'ÂØºÂÖ•Â§±Ë¥•' })
  } finally {
    importLoading.value = false
  }
}

// ÂèñÊ∂àÂØºÂÖ•
const handleImportCancel = () => {
  console.log('[RulesView] ÂèñÊ∂àÂØºÂÖ•')
  showImportModal.value = false
  importFile.value = null
  currentId.value = ''
}

// Á°ÆËÆ§Êñ∞Â¢û/ÁºñËæë
const handleModalConfirm = async () => {
  // Ë°®ÂçïÈ™åËØÅ
  formErrors.name = !formData.name ? 'ËØ∑ËæìÂÖ•ËßÑÂàôÂêçÁß∞' : ''
  formErrors.version = !formData.version ? 'ËØ∑ËæìÂÖ•ÁâàÊú¨Âè∑' : ''
  if (formErrors.name || formErrors.version) {
    return
  }

  try {
    let response
    if (modalType.value === 'add') {
      response = await ruleApi.createRule(formData)
    } else {
      response = await ruleApi.updateRule(currentId.value, formData)
    }

    if (response.code === 200) {
      toast({ 
        type: 'success', 
        message: modalType.value === 'add' ? 'Êñ∞Â¢ûÊàêÂäü' : 'ÁºñËæëÊàêÂäü' 
      })
      showModal.value = false
      fetchRules()
    } else {
      toast({ 
        type: 'error', 
        message: response.message || (modalType.value === 'add' ? 'Êñ∞Â¢ûÂ§±Ë¥•' : 'ÁºñËæëÂ§±Ë¥•')
      })
    }
  } catch (error) {
    toast({ 
      type: 'error', 
      message: modalType.value === 'add' ? 'Êñ∞Â¢ûÂ§±Ë¥•' : 'ÁºñËæëÂ§±Ë¥•' 
    })
  }
}

// Âà†Èô§ËßÑÂàô
const handleDelete = async (rule: Rule) => {
  try {
    if (!confirm(`Á°ÆËÆ§Âà†Èô§ËßÑÂàô"${rule.name}"ÂêóÔºü`)) return;
    
    console.log('[RulesView] ÂºÄÂßãÂà†Èô§ËßÑÂàô:', {
      id: rule.id,
      name: rule.name
    });
    
    const res = await ruleApi.deleteRule(rule.id);
    if (res.code === 200) {
      toast({
        type: 'success',
        message: 'Âà†Èô§ÊàêÂäü'
      });
      fetchRules();
    }
  } catch (error) {
    console.error('[RulesView] Âà†Èô§ËßÑÂàôÂ§±Ë¥•:', error);
    toast({
      type: 'error',
      message: 'Âà†Èô§Â§±Ë¥•'
    });
  }
};

// Ëé∑ÂèñÁä∂ÊÄÅÂØπÂ∫îÁöÑÊ†∑ÂºèÁ±ª
const getStatusColor = (status: string) => {
  switch (status) {
    case 'Â∑≤ÂêØÁî®':
      return 'success';
    case 'Â∑≤ÂØºÂÖ•':
      return 'processing';
    case 'ËçâÁ®ø':
      return 'default';
    case 'Â∑≤Á¶ÅÁî®':
      return 'error';
    default:
      return 'default';
  }
};

// Â§ÑÁêÜ‰∏ä‰º†ÈîôËØØ
const handleUploadError = (error: { type: string; message: string }) => {
  console.log('[RulesView] Êñá‰ª∂‰∏ä‰º†ÈîôËØØ:', error)
  toast({ type: 'error', message: error.message })
}

// ÂàùÂßãÂåñ
onMounted(() => {
  fetchRules()
  fetchDiseaseCategories()
})
</script>

